<template>
  <div class="wishList">
    <van-nav-bar
      fixed
      title="心愿单"
      @click-left="$toPage(-1)"
      @click-right="$toPage('wishAdd')"
      >
      <van-icon name="arrow-left" size="24px" color="#666" slot="left" />
      <van-icon name="plus" size="24px" color="#666" slot="right" />
    </van-nav-bar>
    <van-card
      v-for="(item,index) in list" :key="index"
      :desc="item.manuname"
      :title="item.prodname"
      :price="item.price"
      :num="item.num"
      centered
      >
      <img slot="thumb" :src="imgPath[index]" />
      <div class="tags" slot="tags">{{ item.spec }}</div>
      <div slot="footer">
        {{ state(item.cstatus) }}
      </div>
    </van-card>
    <div v-if="noneImg" class="none"></div>
  </div>
</template>
<script>
import md5 from "md5";
export default {
  data() {
    return {
      list: [],
      imgPath: [],
      noneImg: false
    }
  },
  mounted() {
    this.getWishList()
    this.fileServerInfo()
  },
  methods: {
    getWishList() {
      JNB.query({
        serverName: "goodsServer",
        cls: "WishListModule",
        method: "queryWishList",
        page : 1,
        count : 1000,
        callback: data => {
          let result = JSON.parse(data);
          if(result.code == 200){
            this.list = result.data
            if(this.list.length <= 0) this.noneImg = true
          }
        }
      });
    },
    state(state) {
      switch(state){
          case 0 : return '未处理'
          case 2 : return '未到货'
          case 4 : return '已到货'
      }
    },
    fileServerInfo() {
      JNB.query({
        serverName: "globalServer",
        cls: "FileInfoModule",
        method: "fileServerInfo",
        params: {
          compid: this.$loadUserInfo().comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            var path = result.data.ergodicUrl;
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
              if (xhr.readyState === 4 && xhr.status === 200) {
                var data = xhr.responseText;
                data = JSON.parse(data).data && JSON.parse(data).data.sort();
                this.list.forEach(res => {
                  this.imgPath.push(result.data.downPrev + result.data.wishListPath + '/' + md5(res.prodname + res.spec + res.manuname) + '.jpg')
                })
              }
            };
            xhr.open("POST", path, true);
            xhr.setRequestHeader("specify-path", result.data.wishListPath);
            xhr.setRequestHeader("ergodic-sub", "false");
            xhr.send(null);
          }
        }
      });
    },
  }
}
</script>

<style>
.wishList{
  padding-top: 82px;
}
.van-card__content{
  font-size: 24px;
}
.van-card__content div{
  margin: 3px;
}
.van-card__thumb{
  margin-right: 40px;
}
.van-card__thumb img{
  width: 100%;
  height: 100%;
}
.van-card__footer div{
  font-size: 28px;
  color:rgba(237,47,38,1);
}
.none{
  background: url('../../assets/noneImg/xinyuan.png') no-repeat center center;
  background-size: 50%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
</style>
